import React, { useState, useEffect } from 'react';
import axios from 'axios';
import TablaCarreras from './../tables/TablaCarreras.jsx';



let hoy = new Date();



const ModalRace = ({ children, isOpen, closeModal, propsi }) => {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [showData, setShowData] = useState(false);
  let identidad=propsi.identidad;

 useEffect(() => {
   setLoading(true)
    axios.get('http://localhost:4000/race?status=create')
      .then(response => setData(response.data.body))
      .then(() => setLoading(false))
     
  }, []);

  const handleModalContainerClick = (e) => e.stopPropagation();

    
const handleFetch = async () => {
if (showData === false){
setShowData(true) }else{   setShowData(false)}

};





  return (
    <article className={`modal ${isOpen && "is-open"}`} onClick={closeModal}>
      <div className="modal-container" onClick={handleModalContainerClick}>
        <button className="modal-close" onClick={closeModal}>
          X
        </button>
         <div className="card-info">
       
          <div className="content"> 
           <div className="content-img"> 
         <img src={propsi.Imagen} alt={propsi.name} className="icono"  />
         </div>
            <div className="name">
          <p>{propsi.name}</p>
         </div>
                 <div className="data-container" style={{ display: showData ? 'block' : 'none' }}>
  {data && ( 

    <TablaCarreras data={data} propsi={propsi} fecha={hoy}  />
   
  )}
        </div>
         <div className="actions">
<input type="submit" name="background"  value="Ver Carreras Disponibles"  onClick={() => handleFetch() }/>


</div>
       </div>
       </div>
        </div>

    </article>
  );
};

export default ModalRace;
