import React, { useState, useEffect } from "react";
import axios from 'axios';

import './components/cardballos.css'

import iconNegocios from'./../../assets/home-broker-dealer-icon.svg'
import iconCarrera from'./../../assets/horse-riding-icon.svg'
import infoballo from'./../../assets/horseshoe-icon.svg'
import ModalBallo from"./components/models/InfoballoModal.jsx"
import ModalRace from"./components/models/RaceBalloModal.jsx"
import ModalNegoti from"./components/models/NegotiationModal.jsx"
import  "./components/models/infoballoModal.css"

import Loader from"./../generals/loders/Loader.jsx"
//Impor de iconos SVG para infoballo
import salucita from"./../../assets/components/svg/salud/heart-svgrepo-com.svg"
import temperatura from"./../../assets/components/svg/temperamentp/calmness-svgrepo-com.svg"



 //key, Imagen, name,sex,salud,resiste,addResi,temperamento,addTemperamento 


let initialValue=false


function Ballo({...props}) {
   const [isOpenInf, setIsOpenInf] = useState(initialValue);

  const openModalInf = () => setIsOpenInf(true);

  const closeModalInf = () => setIsOpenInf(false);



  const [isOpen, setIsOpen] = useState(initialValue);

  const openModal = () => setIsOpen(true);

  const closeModal = () => setIsOpen(false);


   const [isOpenNeg, setIsOpenNeg] = useState(initialValue);

  const openModalNeg = () => setIsOpenNeg(true);

  const closeModalNeg = () => setIsOpenNeg(false);




   return (
    <div className="card">
    <div className="box">
      <div className="content">
        <h2>{props.Key}</h2>
   
      
        <img src={props.Imagen} alt="algo aqui" className="caja" />
        <p>{props.name}</p>
        <p><spam>Mint : </spam>{props.identidad}   </p>
         <h2>{props.key}</h2>
    
    
        <img src={iconNegocios} alt={props.name} className="icono" onClick={openModalNeg} />
       
        <img src={iconCarrera} alt={props.name} className="icono" onClick={openModal}/>
      
      
  
   <img src={infoballo} alt={props.name} className="icono"  onClick={openModalInf}/>

      
  



      </div>
    </div>
        <ModalBallo isOpen={isOpenInf} closeModal={closeModalInf} propsi={props}>

         <button className="modal-close" onClick={closeModalInf}>
        X
        </button>


    </ModalBallo>



   <ModalRace isOpen={isOpen} closeModal={closeModal}  propsi={props}>
      
    </ModalRace>

   
    <ModalNegoti isOpenNeg={isOpenNeg} closeModalNeg={closeModalNeg}  propsi={props}>
   
      
    </ModalNegoti>
    </div>

  );
}




    


export default function AjaxHooks() {
  const [Ballos, setBallos] = useState([]);
const [isLoaded, setisLoaded] = useState(false);


useEffect(() => {
    
 const getBallos = async (url) => {

      let config = {
      headers: {
        "Content-Type": "application/json",
        "Cache-Control": "no-cache",
      },
    };

    let query={}
      query.account = localStorage.getItem("user");
    


    let body = JSON.stringify([
      {
        method: "find",
        jsonrpc: "2.0",
        params: {
          contract: 'nft',
          table: "BALLSinstances",
          query: query,
          limit: 1000,
          offset: 0,
          indexes: [],
        },
        id: 1,
      },
    ]);

     await axios.post(url, body, config).then((data) => {
     
      data =data.data[0].result;
      
 data.forEach( (data,indexes) => {
// ACA VAN TODAS LAS PROPIEDADES DE UN BALLO O GUA

let Type=data.properties.TYPE
//Inherent:
let identidad=data._id;
let Generacion=data.properties.INHERENT.substr(0,3)
// console.log("Generacion", Generacion);
let Minteo=data.properties.INHERENT.substr(4,8)
// console.log("Minteo", Minteo);
let Sexo=data.properties.INHERENT.substr(13,1)
// console.log("Sexo", Sexo);
let Salud=data.properties.INHERENT.substr(15,3)
// console.log("Salud", Salud);
let Resistencia=data.properties.INHERENT.substr(19,3)
// console.log("Resistencia", Resistencia);
let AddResistencia=data.properties.INHERENT.substr(23,3)
// console.log("AddResistencia", AddResistencia);
let Temperamento=data.properties.INHERENT.substr(27,3)
// console.log("Temperamento", Temperamento);
let AddTemperamento=data.properties.INHERENT.substr(31,3)
console.log("AddTemperamento", AddTemperamento);
let Morfologia=data.properties.INHERENT.substr(35,3)
// console.log("Morfologia", Morfologia);
let Nacimiento=data.properties.INHERENT.substr(39,16)
console.log("Nacimiento", Nacimiento);
let Diasdevida=data.properties.INHERENT.substr(56,4)
// console.log("Diasdevida", Diasdevida);
let CarrerasUtiles=data.properties.INHERENT.substr(61,4)
// console.log("CarrerasUtiles", CarrerasUtiles);
let STATUS=data.properties.INHERENT.substr(66,1)
// console.log("STATUS", STATUS);
let NAME=data.properties.INHERENT.substr(68,25)
console.log("NAME", NAME);

//ADDED:
  //console.log("ADDED ADDED ADDED ADDEDADDED  ADDED ADDED ADDED ADDED ADDED ADDED ADDED");


let Animo=data.properties.ADDED.substr(0,3);
// console.log("animo", Animo);
let GanadasConsecutivas=data.properties.ADDED.substr(4,3);
// console.log("GanadasConsecutivas", GanadasConsecutivas);
let perdidasConsecutivas=data.properties.ADDED.substr(8,3);
// console.log("perdidasConsecutivas", perdidasConsecutivas);
let GanadasTotal=data.properties.ADDED.substr(12,4);
// console.log("GanadasTotal", GanadasTotal);
let CarrerasCorrida=data.properties.ADDED.substr(17,4);
// console.log("CarrerasCorrida", CarrerasCorrida);
let DiaDescanzo=data.properties.ADDED.substr(22,16);
// console.log("DiaDescanzo", DiaDescanzo);
let IPFS="https://ipfs.io/ipfs/" + data.properties.ADDED.substr(39,60);
// console.log("IPFS", IPFS);

  let Ballo = {
          id: Minteo,
          identidad:identidad,
          tipo: Type,
          generacion: Generacion,
          sexo: Sexo,
          salud: Salud,
          resistencia: Resistencia,
          addResistencia: AddResistencia,
          temperamento: Temperamento,
          addTemperamento: AddTemperamento,
          morfologia: Morfologia,
          nacimiento: Nacimiento,
          diasdevida: Diasdevida,
          carrerasUtiles: CarrerasUtiles,
          status: STATUS,
          name: NAME,
          animo: Animo,
          ganadasConse: GanadasConsecutivas,
          perdidasConse: perdidasConsecutivas,
          ganadasTotal: GanadasTotal,
          carrerasCorridas: CarrerasCorrida,
          diaDescanzo: DiaDescanzo,
          imagen: IPFS,
        };

        setBallos((Ballos) => [...Ballos, Ballo]);
      });
    

      }).catch((err)=>{  
           console.log("error fetching",err);
                } 
         ).finally(()=>{console.log("se ejecuta finally");
          setisLoaded(true)
      })
    
   };
    getBallos("https://enginetestnet.rishipanthee.com/contracts");

  },[]);

  return (
    <>
             <div className="wrapp">
      <div className="container">
      {((Ballos.length === 0) &&(isLoaded===false)) ? (
        <Loader/>

      ) : (
       

        Ballos.map((el,index) => (
           
          <Ballo key={el.index}  tipo= {el.tipo} identidad={el.identidad} minteo={el.id}genera={el.generacion} sex={el.sexo} salud={el.salud} 
          resiste={el.resistencia}  addResi={el.addResistencia} 
          temperamento={el.temperamento} addTempe={el.addTemperamento}  
          morfologia={el.morfologia} nacimiento={el.nacimiento} 
          diasdevida={el.diasdevida} carrerasUtiles={el.carrerasUtiles} 
          status={el.status} name={el.name} animo={el.animo} 
          ganadasConse={el.ganadasConse} 
          perdidasConse={el.perdidasConse} ganadasTotal={el.ganadasTotal} 
          carrerasTotals={el.carrerasCorridas} diaDescanzo={el.diaDescanzo} Imagen={el.imagen} />
         

      
         



        ))
         
      )}
      </div></div>
    </>
  );
}

